@import url(https://fonts.googleapis.com/css2?family=Cormorant:ital,wght@0,300..700;1,300..700&display=swap);
[id='root'] {
  --primary-color:#FAF4ED;
  --secondary-color: #4D2F1A;
  --tertiary-color:#DEC9B6;
}

html {
  scroll-behavior: smooth;
}

body {
  display: flex;
  flex-direction: column;
  margin: 0;
  box-sizing: border-box;
  min-height: 100vh;
  font-family: 'Cormorant', sans-serif;
  font-size: 14px;
  font-weight: 400;
  background-color: #FAF4ED ;

  @media (min-width: 768px) {
    font-size: 16px;
  }

  @media (min-width: 1200px) {
    font-size: 18px;
  }
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

figure {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
  color: inherit;
}

h1 {
  font-size: 12px;
  font-weight: 500;
  text-align: center;
  letter-spacing: 2px;
  text-transform: uppercase;
  line-height: 1.5;

  @media (min-width: 767px) {
    top: 65%;
    left: 38%;
    font-size: 18px;
    letter-spacing: 10px;
    line-height: 1.5;

  }

  @media (min-width: 1200px) {
    top: 50%;
    left: 42%;
    font-size: 24px;
  }

}


h2 {
  margin: 0;
  padding: 20px;
  color: var(--secondary-color);
  text-transform: uppercase;
  font-size: 26px;
  font-weight: 600;

  @media (min-width: 768px) {
    font-size: 20px;
  }
}

h3 {
  margin: 0;
  padding: 20px;
  font-size: 24px;
  font-weight: 600;
  text-transform: uppercase;

  @media (min-width: 768px) {
    font-size: 22px;
  }
}
[id='home'] {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

[id='moment'] {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;
    width: 100%;
    box-sizing: border-box;

    h2 {
        align-self: center;
    }

    div {
        display: flex;
        flex-direction:row;
        grid-gap: 20px;
        gap: 20px;
        padding: 20px;
        width: 100%;
    }

    .about {
        display: flex;
        flex-direction: row;
        grid-gap: 20px;
        gap: 20px;
        width: 100%;
    }

    

    
}






[id='nav'] {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1 {
        position: absolute;
        font-style: italic;
        text-transform: uppercase;
        top: 300px;
        right: 10px;
        color: var(--secondary-color);
    }


    ul {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 10px;
        grid-gap: 5px;
        gap: 5px;
        font-size: 16px;;
        text-transform: uppercase;

        @media (min-width: 768px) {

            display: flex;
            flex-direction: row;
            grid-gap: 20px;
            gap: 20px;
        }

    }

    li {
        cursor: pointer;
        transition: transform 0.2s;

        &:hover {
            transform: scale(1.1);
            color: var(--secondary-color);
            font-weight: 700;
        }
    }

    img {
        margin: 0;
        width: 100%;
        height: 655px;
        object-fit: cover;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
    }
}
[id='footer'] {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    text-align: center;
    font-size: 0.8rem;
    color: var(--secondary-color);

    figure {
        display: flex;
        flex-direction: row;
        align-items: center;
        grid-gap: 500px;
        gap: 500px;
    }

    img {
        width: 70px;
        height: 70px;
        border-radius: 50%;
    }
}

.scroll-to-top-button {
    position: fixed;
    bottom: 50px;
    right: 30px;
    width: 30px;
    height: 30px;
    background-color: var(--secondary-color);
    color: #fff;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: all 0.3s ease;

    @media (min-width: 768px) {
      right: 20px;
  }

  @media (min-width: 1200px) {
      right: 40px;

  } 
  }
  
  .scroll-to-top-button:hover {
    transform: translateY(-10px);
  }
  
  .scroll-to-top-button::after {
    content: "Revenir en haut";
    position: absolute;
    bottom: -42px;
    left: 40%;
    transform: translateX(-50%);
    color:black;
    font-size: 14px;
    opacity: 0;
    transition: opacity 0.3s ease;

    @media (min-width: 768px) {
        left: 50%;
    }
  }
  
  .scroll-to-top-button:hover::after {
    opacity: 1;
  }
  
